











































import Vue from 'vue';
import axios from 'axios';
import 'vuetify/dist/vuetify.min.css';
import { Component, Watch } from 'vue-property-decorator';

@Component({
  components: {},
})
export default class App extends Vue {
  get loggedIn() {
    return this.$store.getters.isLoggedIn;
  }

  get admin() {
    return this.$store.getters.isAdmin;
  }

  private links = [
    {
      name: 'Dashboard',
      route: '/',
      show: true,
    },
    {
      name: 'Testfälle',
      route: '/test',
      show: true,
    },
    {
      name: 'Testreihen',
      route: '/testgroup',
      show: true,
    },
    {
      name: 'Korpus',
      route: '/publishers',
      show: true,
    },
    {
      name: 'Benutzer',
      route: '/user',
      show: false,
    },
    /* {
      name: 'Manuelle Tests',
      route: '/test/manuell',
      show: false,
    }, */
    {
      name: 'Kontroll',
      route: '/controlpanel',
      show: false,
    },
  ];

  logOut() {
    axios.get('/api/user/logout').then(() => {
      this.$store.dispatch('logout');
      this.$router.push('/');
    });
  }

  editProfile() {
    this.$router.push(`/user/edit/${this.$store.getters.getUser.id}`);
  }

  @Watch('$route')
  onRouteChange() {
    this.$store.dispatch('checkStatus');
  }
}
